import { Component, OnInit } from '@angular/core';
import { ProductService } from '../../../../../../auth/_services/product.service';
import { QuickSidebarComponent } from '../../../../../layouts/quick-sidebar/quick-sidebar.component';
// import { SideBarService } from '../../../../layouts/side-bar/side-bar.service';
import { SideBarToggleComponent } from '../../../../../layouts/side-bar-toggle/side-bar-toggle.component';
import { DataService } from '../../../../../../_services/data.service';
import { ActivatedRoute } from '@angular/router';
@Component({
    selector: 'app-shareable-product-detail',
    templateUrl: "shareable-product-detail.component.html",
    styles: []
})
export class ShareableProductDetailComponent implements OnInit {


    private formSumitAttempt: boolean;
    public productDetails: any = {};
    private productId: any;
    public showAddBtn = 0;
    constructor(private route: ActivatedRoute, private _dataService: DataService, private _productService: ProductService) { }

    ngOnInit() {
        let sharedVendor: any;
        let vendors: any;
        let loggedVendorId: any;
        let indexValue: any;
        this._dataService.setloaderState(false);
        this._dataService.setactiveMenu('shareable-product');
        this.route.params.subscribe(
            params => {
                this.productId = params.id;
                this._productService.getById(params.id).subscribe(
                    data => {
                        this.productDetails = data.success[0];
                        loggedVendorId = JSON.parse(localStorage.getItem('currentUser'));
                        sharedVendor = this.productDetails.shared_vendor;
                        console.log(sharedVendor != null);
                        if (loggedVendorId.success.role == 1) {
                            this.showAddBtn = 2;
                        } else if (loggedVendorId.success.vendor_id && sharedVendor) {
                            vendors = sharedVendor.split(',');
                            indexValue = vendors.indexOf(loggedVendorId.success.vendor_id);
                            if (indexValue >= 0) {
                                this.showAddBtn = 1;
                            }
                        }
                    }
                );
            }
        );


    }

    public addProductInStore() {
        let current_logged_user_id = JSON.parse(localStorage.getItem('currentUser'));
        current_logged_user_id = current_logged_user_id.success.vendor_id;
        console.log(current_logged_user_id);
        if (current_logged_user_id) {
            this._productService.addShareableProduct({ vendor_id: current_logged_user_id, product_id: this.productId }).subscribe(
                data => {
                    if (data.success == 1) {
                        this.showAddBtn = 1;
                        this._productService.getNotification(current_logged_user_id).subscribe(
                            datas => {
                                this._dataService.setNotification(datas.success.length);
                                console.log(datas.success.length);
                            }
                        );
                        this._dataService.getNotification().subscribe(
                            not_data => {
                                console.log('not_data', not_data);
                            }
                        )
                    }
                }
            );
        }
    }

}
