import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ShareableProductDetailComponent } from '../shareable-product-detail/shareable-product-detail.component';
import { DefaultComponent } from '../../../../default/default.component';
import { RouterModule, Routes } from '@angular/router';
import { LayoutModule } from '../../../../../layouts/layout.module';
import { FormsModule } from '@angular/forms';
import { NgxPaginationModule } from 'ngx-pagination';
const routes: Routes = [
    {
        path: "",
        component: DefaultComponent,
        children: [
            {
                path: "",
                component: ShareableProductDetailComponent
            }
        ]
    },
];

@NgModule({
    imports: [
        CommonModule, RouterModule.forChild(routes),
        LayoutModule, FormsModule, NgxPaginationModule,
    ],
    declarations: [ShareableProductDetailComponent],
    // providers:[DataService]
})
export class ShareableProductDetailModule { }
